import React from "react";
import ReactDOM from "react-dom";
import { Layout, Menu } from "antd";
import "antd/dist/antd.css";

const { Header, Content, Footer } = Layout;

const App = () => {
  return (
    <Layout className="layout">
      <Header style={{ height: "10vh", width: "100vw" }}>
        <div className="logo" />
        <Menu
          theme="light"
          mode="horizontal"
          defaultSelectedKeys={["1"]}
          style={{ lineHeight: "9vh", textAlign: "right" }}
        >
          <Menu.Item key="1">Home</Menu.Item>
          <Menu.Item key="2">Core Team</Menu.Item>
        </Menu>
      </Header>
      <Content style={{ padding: "", height: "80vh" }}>
        <div
          className="main-content"
          style={{ paddingLeft: "8vw", paddingTop: "5vh", height: "100%" }}
        >
          Content
        </div>
      </Content>
      <Footer style={{ paddingLeft: "8vw", height: "10vh" }}>Footer</Footer>
    </Layout>
  );
};

ReactDOM.render(<App />, document.querySelector("#root"));
